<template>
  <div class="more-info">
      <div class="controls" @click="open = !open"></div>
      <transition
        v-on:enter="show"
        v-on:leave="hide"
        v-bind:css="false"
      >
        <div v-if="open" class="content">
          <p>{{ content }}</p>
        </div>
      </transition>
  </div>
</template>

<script>
  import Velocity from 'velocity-animate'

  export default {
    data() {
      return {
        open: true,
      };
    },

    props: {
        content:'Hello World'
    },

    methods: {
      show: function (el, done) {
        Velocity(el, { width: "200px" }, { duration: 600, easing: "linear", complete: done })
      },
      hide: function (el, done) {
        Velocity(el, {width: "0"}, {duration: 600, easing: "linear", complete: done})
      },
    }
  }
</script>

<style lang="scss" scoped>
  .more-info {
    position: absolute;
    top: 10px;
    right: 0;
    height: 80%;
    z-index: 5000;
    background-color: #0086b3;
    opacity: 0.8;

    .content {
      display: inline-block;
      position: relative;
      height: 100%;

      p {
        position: relative;
      }
    }

    .controls {
      display: inline-block;
      opacity: 10;
      width: 15px;
      height: 100%;
      background-color: #1d3e81;

      &:hover {
        cursor: pointer;
      }
    }
    *, *::after, *::before {
      box-sizing: inherit;
    }

  }
</style>



